// dependencies
import React ,{Component} from 'react'


class Goal extends Component {
    render() {
        return (
            <div className='goal-container'>
                <div className='goal-container-helper'>
                    <p>Cele ankiety:</p>
                    <span> 
                        <ul>
                            <li>Jakie s rda finansowania poszczeglnych osb oraz ktre paszczyzny ycia pobieraj najwicej dochodw?</li>
                            <li>Czy da si wyy w Krakowie (innym duym lub mniejszym miecie) za rednie dochody dla studentw na 2/3/4 roku bez zewntrznego finansowania?</li>
                            <li>Gdzie mona szuka oszczdnoci?</li>
                        </ul>
                    </span>
                </div>
            </div>
        )
    }
}

export default Goal


